import React from 'react';
import {connect} from 'react-redux';
import {Link} from 'react-router-dom';


class Quiz extends React.Component{

  componentWillMount(){
    //let token = localStorage.getItem('token');
    //if(!this.props.user.name && !token) this.props.history.push('/login')
    if(!this.props.user.name) this.props.history.push('/login');
  }

  render(){
    const colours=this.props.colours.colours.map((colour,i)=>{
      return <Link
          to={`/quiz/game/${colour.colour}`}
          key={i}>
        <div
            className="colour"
            onClick={()=>this.props.makeCurrent(colour.colour)}
            style={{backgroundColor:colour.colour}}
            key={i}>
        </div>
      </Link>
    });

    return(
        <div>
        <div className="content">
          <h1 className="choose-colour">pasirinkite spalv:</h1>
          {colours}
        </div>
          <div
              className="chalkboard"
          >
            <span>taisykls</span>
            <div className="text">
aidimo tikslas - palaipsniui sunkjant sudtingumui, kiekviename lygyje pasirinkti 5 i bendro konteksto isiskirianias spalvas.
            Pasirinkus maiau u 5 ar pasirinkus bent vien netinkam, aidjas pralaimi.
            </div>
          </div>


        </div>
    );
  };
}

const mapStateToProps=(state)=>{
  return{
    colours:state.colours,
    user:state.user
  }
};

const mapDispatchToProps=(dispatch)=>{
  return{
    makeCurrent(colour){
      dispatch({type:'MAKE_CURRENT', payload:colour})
    }
  };
};

export default connect(mapStateToProps,mapDispatchToProps)(Quiz);


